const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			collectionList:[],
			imgUrl:'',
			ItemId: ''
		}
	},
	onLoad(e) {
		this.ItemId = e.id
		this.getList();
		this.imgUrl = helper.imgUrl
		uni.setNavigationBarTitle({
			title: e.name
		})
	},
	methods: {
		getList(){
			uni.request({
				url: `${helper.requestUrl}/system-tools/category-tool`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					tool_id: this.ItemId
				},
				success: res => {
					res = helper.null2str(res)
					if (res.data.status_code == 200) {
						this.collectionList = res.data.children
					} else {
						
					}
				}
			})
		},
		getData(e){
			let type = e.currentTarget.dataset.type
			let extra = e.currentTarget.dataset.link
			let id = e.currentTarget.dataset.id
			let name = e.currentTarget.dataset.name
			if(type == 'block'){
				if (extra != '') {
					uni.navigateTo({
						url:`/pages/${extra}`
					})
				} else {
					uni.showToast({
						title: '此页面不存在'
					});
				}
			} else if(type == 'series'){
				
			} else if(type == 'post'){
				uni.navigateTo({
					url:`/pages/articleDetail?id=${id}`
				})
				// 帖子
			} else if(type == 'child'){
				// 应用子
				console.log(id)
				uni.navigateTo({
					url:`/pages/applyShow?id=${id}&name=${name}`
				})
			} else if(type == 'ex_link'){
				if(extra == ''){
					uni.showToast({
						title: '此页面不存在',
						icon: 'none'
					})
				}else{

							uni.showToast({
								title:'该小程序在不支持，请下载App',
								icon: 'none'
							})








				}
				// 外联
			} else if(type == 'category'){
				uni.navigateTo({
					url:`/pages/applyShow?id=${id}`
				})
			}
		}
	}
}
